<template>
    <div class="project_list">
        <div class="loader" v-show="ShowLoader">
            <svg version="1.1" id="loader-1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="40px" height="40px" viewBox="0 0 50 50" style="enable-background:new 0 0 50 50;" xml:space="preserve">
                <path fill="#fff" d="M25.251,6.461c-10.318,0-18.683,8.365-18.683,18.683h4.068c0-8.071,6.543-14.615,14.615-14.615V6.461z" transform="rotate(137.96 25 25)">
                    <animateTransform attributeType="xml" attributeName="transform" type="rotate" from="0 25 25" to="360 25 25" dur="0.6s" repeatCount="indefinite"></animateTransform>
                </path>
            </svg>
        </div>
        <navigation></navigation>
        <profile></profile>
        <logo></logo>
        <div class="content_holder container-fluid">
            <div class="row">
                <div class="col-md-3 col-sm-3 col-xs-12"></div>
                <div class="col-md-9 col-sm-9 col-xs-12">
                    <h4 class="form_title">
                        لیست پروژه ها
                    </h4>
                    <p class="form_desc">
                        در این لیست شما می توانید پروژه های درج شده خود را حذف و تدوین نمایید.
                    </p>
                </div>
                <div class="clearfix"></div>
                <div class="col-md-3 col-sm-3 col-xs-12"></div>
                <div class="col-md-9 col-sm-9 col-xs-12">
                    <div class="table-responsive">
                        <table class="table table-striped">
                            <thead>
                                <tr>
                                    <th>نام پروژه</th>
                                    <th>زمان درج</th>
                                    <th>حذف</th>
                                    <th>تدوین</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item, index) in list">
                                    <td>
                                        {{item.data.title}}
                                    </td>
                                    <td>
                                        {{item.data.date}}
                                    </td>
                                    <td>
                                        <button @click="RemoveProject(index,item)" class="remove_project">حذف</button>
                                    </td>
                                    <td>
                                        <button @click="EditProject(item.id)" class="edit_project">تدوین</button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Menu from './Menu'
import Logo from './Logo'
import Profile from './Profile'

export default {
    name:'project',
    data() {
        return {
            ShowLoader:false,
            list:[]
        }
    },
    components:{
        'navigation': Menu,
        'logo': Logo,
        'profile': Profile
    },
    created(){
        return this.GetData(),
        document.title = "لیست پروژه ها";
    },
    methods : {
        GetData : function(){
            this.$http.get('http://panel.hex.team/api/getwidget/project',{
                headers:{
                    'Authorization':localStorage.getItem('Authorization')
                }
            })
            .then(function(res) {
                console.log(res.data);
                for(var i = 0;res.data.length > i; i++){
                    this.list.push({data:JSON.parse(res.data[i].data),id:res.data[i].id});
                    console.log(this.list[i]);
                }
            },function(err){
                console.log(err);
            });
        },
        RemoveProject(index,item){
            this.list.splice(index,1);
        },
        EditProject(id){
            console.log(id);
            this.$router.push({ name: 'editproject', query: { id: id }})
        }
    }
}
</script>

<style>
.project_list .content_holder{
    position: absolute;
    left: 80px;
    top: 100px;
    right: 40px;
    bottom: 80px;
    padding: 0;
    z-index: 99;
}
.project_list .form_title{
    text-align: right;
    font-family: "IRANSANS BOLD";
    padding-bottom: 10px;
    font-size: 1.5em;
}
.project_list .form_desc{
    text-align: right;
    padding-bottom: 10px;
    font-size: 1em;
    direction: rtl;
    margin-bottom: 25px;
}
.table{
    table-layout: fixed;
    direction: rtl;
}
.table>thead>tr>th,
.table>tbody>tr>td{
    text-align: center;
}
.remove_project,
.edit_project{
    display: inline-block;
    background: #71e0ca;
    border: 1px solid transparent;
    border-radius: 6px;
    padding: 7px;
    outline: none;
    white-space: nowrap;
    -webkit-user-select: none;
    cursor: pointer;
    color: #fff;
    font-weight: 700;
    font-size: 15px;
    font-weight: bold;
    box-shadow: 0px 5px 0px #55c9b2;
    transition: all .2s ease-in;
}
.remove_project{
    background: #D91E18;
    box-shadow: 0px 5px 0px #C0392B;
}
.remove_project:hover{
    box-shadow: 0px 2px 0px #C0392B;
    transition: all .2s ease-in;
}
.edit_project:hover{
    box-shadow: 0px 2px 0px #55c9b2;
    transition: all .2s ease-in;
    text-decoration: none;
}
.loader{
    position: fixed;
    z-index: 9999;
}
</style>


